// Service pour grer le coffre-fort numrique (mots de passe, cls API, etc.)
import { v4 as uuidv4 } from 'uuid';
import CryptoJS from 'crypto-js';

// Cl pour le stockage local
const VAULT_KEY = 'secure_vault_data';
const MASTER_PASSWORD_HASH_KEY = 'master_password_hash';
const VAULT_INITIALIZED_KEY = 'vault_initialized';

// Fonction pour vrifier si le coffre-fort est initialis
export const isVaultInitialized = () => {
  try {
    return localStorage.getItem(VAULT_INITIALIZED_KEY) === 'true';
  } catch (error) {
    console.error('Erreur lors de la vrification de l\'initialisation du coffre-fort:', error);
    return false;
  }
};

// Fonction pour initialiser le coffre-fort avec un mot de passe matre
export const initializeVault = (masterPassword) => {
  try {
    if (isVaultInitialized()) {
      return { success: false, message: 'Le coffre-fort est dj initialis.' };
    }

    // Hasher le mot de passe matre
    const passwordHash = CryptoJS.SHA256(masterPassword).toString();
    
    // Stocker le hash du mot de passe
    localStorage.setItem(MASTER_PASSWORD_HASH_KEY, passwordHash);
    
    // Initialiser le coffre-fort vide et le chiffrer
    const emptyVault = [];
    const encryptedVault = CryptoJS.AES.encrypt(
      JSON.stringify(emptyVault),
      masterPassword
    ).toString();
    
    // Stocker le coffre-fort chiffr
    localStorage.setItem(VAULT_KEY, encryptedVault);
    localStorage.setItem(VAULT_INITIALIZED_KEY, 'true');
    
    return { success: true, message: 'Coffre-fort initialis avec succs.' };
  } catch (error) {
    console.error('Erreur lors de l\'initialisation du coffre-fort:', error);
    return { success: false, message: 'Erreur lors de l\'initialisation du coffre-fort.' };
  }
};

// Fonction pour vrifier le mot de passe matre
export const verifyMasterPassword = (masterPassword) => {
  try {
    const storedHash = localStorage.getItem(MASTER_PASSWORD_HASH_KEY);
    if (!storedHash) {
      return false;
    }
    
    const inputHash = CryptoJS.SHA256(masterPassword).toString();
    return storedHash === inputHash;
  } catch (error) {
    console.error('Erreur lors de la vrification du mot de passe matre:', error);
    return false;
  }
};

// Fonction pour obtenir tous les lments du coffre-fort
export const getVaultItems = (masterPassword) => {
  try {
    if (!verifyMasterPassword(masterPassword)) {
      return { success: false, message: 'Mot de passe matre incorrect.', data: [] };
    }
    
    const encryptedVault = localStorage.getItem(VAULT_KEY);
    if (!encryptedVault) {
      return { success: true, data: [] };
    }
    
    // Dchiffrer le coffre-fort
    const bytes = CryptoJS.AES.decrypt(encryptedVault, masterPassword);
    const decryptedVault = JSON.parse(bytes.toString(CryptoJS.enc.Utf8));
    
    return { success: true, data: decryptedVault };
  } catch (error) {
    console.error('Erreur lors de la rcupration des lments du coffre-fort:', error);
    return { success: false, message: 'Erreur lors de la rcupration des lments du coffre-fort.', data: [] };
  }
};

// Fonction pour ajouter un lment au coffre-fort
export const addVaultItem = (masterPassword, item) => {
  try {
    const result = getVaultItems(masterPassword);
    
    if (!result.success) {
      return result;
    }
    
    const vaultItems = result.data;
    
    // Ajouter un ID unique et la date de cration
    const newItem = {
      ...item,
      id: uuidv4(),
      createdAt: new Date().toISOString(),
      updatedAt: new Date().toISOString()
    };
    
    vaultItems.push(newItem);
    
    // Chiffrer et stocker le coffre-fort mis  jour
    const encryptedVault = CryptoJS.AES.encrypt(
      JSON.stringify(vaultItems),
      masterPassword
    ).toString();
    
    localStorage.setItem(VAULT_KEY, encryptedVault);
    
    return { success: true, message: 'lment ajout avec succs.', data: newItem };
  } catch (error) {
    console.error('Erreur lors de l\'ajout d\'un lment au coffre-fort:', error);
    return { success: false, message: 'Erreur lors de l\'ajout d\'un lment au coffre-fort.' };
  }
};

// Fonction pour mettre  jour un lment du coffre-fort
export const updateVaultItem = (masterPassword, id, updatedItem) => {
  try {
    const result = getVaultItems(masterPassword);
    
    if (!result.success) {
      return result;
    }
    
    const vaultItems = result.data;
    const itemIndex = vaultItems.findIndex(item => item.id === id);
    
    if (itemIndex === -1) {
      return { success: false, message: 'lment non trouv.' };
    }
    
    // Mettre  jour l'lment
    vaultItems[itemIndex] = {
      ...vaultItems[itemIndex],
      ...updatedItem,
      updatedAt: new Date().toISOString()
    };
    
    // Chiffrer et stocker le coffre-fort mis  jour
    const encryptedVault = CryptoJS.AES.encrypt(
      JSON.stringify(vaultItems),
      masterPassword
    ).toString();
    
    localStorage.setItem(VAULT_KEY, encryptedVault);
    
    return { success: true, message: 'lment mis  jour avec succs.', data: vaultItems[itemIndex] };
  } catch (error) {
    console.error('Erreur lors de la mise  jour d\'un lment du coffre-fort:', error);
    return { success: false, message: 'Erreur lors de la mise  jour d\'un lment du coffre-fort.' };
  }
};

// Fonction pour supprimer un lment du coffre-fort
export const deleteVaultItem = (masterPassword, id) => {
  try {
    const result = getVaultItems(masterPassword);
    
    if (!result.success) {
      return result;
    }
    
    const vaultItems = result.data;
    const updatedItems = vaultItems.filter(item => item.id !== id);
    
    if (vaultItems.length === updatedItems.length) {
      return { success: false, message: 'lment non trouv.' };
    }
    
    // Chiffrer et stocker le coffre-fort mis  jour
    const encryptedVault = CryptoJS.AES.encrypt(
      JSON.stringify(updatedItems),
      masterPassword
    ).toString();
    
    localStorage.setItem(VAULT_KEY, encryptedVault);
    
    return { success: true, message: 'lment supprim avec succs.' };
  } catch (error) {
    console.error('Erreur lors de la suppression d\'un lment du coffre-fort:', error);
    return { success: false, message: 'Erreur lors de la suppression d\'un lment du coffre-fort.' };
  }
};

// Fonction pour changer le mot de passe matre
export const changeMasterPassword = (currentPassword, newPassword) => {
  try {
    if (!verifyMasterPassword(currentPassword)) {
      return { success: false, message: 'Mot de passe matre actuel incorrect.' };
    }
    
    // Rcuprer les lments actuels
    const result = getVaultItems(currentPassword);
    
    if (!result.success) {
      return result;
    }
    
    const vaultItems = result.data;
    
    // Hasher le nouveau mot de passe
    const newPasswordHash = CryptoJS.SHA256(newPassword).toString();
    
    // Chiffrer le coffre-fort avec le nouveau mot de passe
    const encryptedVault = CryptoJS.AES.encrypt(
      JSON.stringify(vaultItems),
      newPassword
    ).toString();
    
    // Stocker le nouveau hash et le coffre-fort rechiffr
    localStorage.setItem(MASTER_PASSWORD_HASH_KEY, newPasswordHash);
    localStorage.setItem(VAULT_KEY, encryptedVault);
    
    return { success: true, message: 'Mot de passe matre chang avec succs.' };
  } catch (error) {
    console.error('Erreur lors du changement du mot de passe matre:', error);
    return { success: false, message: 'Erreur lors du changement du mot de passe matre.' };
  }
};

// Exporter les fonctions du service
export const vaultService = {
  isVaultInitialized,
  initializeVault,
  verifyMasterPassword,
  getVaultItems,
  addVaultItem,
  updateVaultItem,
  deleteVaultItem,
  changeMasterPassword
}; 